.collapse {
  width: 100%;
  max-width: 144rem;
  margin: 0 auto;
  border-radius: 0.625rem;
  background-color: rgb(255, 96, 96);
  overflow: hidden;
  height: 5.4rem;
  transition: height 0.3s ease;
}
.collapse.open {
  height: auto;
}
.collapse-header {
  height: 5.4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 2.4rem;
  font-size: 1.6rem;
  color: white;
  cursor: pointer;
}
.collapse-header .chevron {
  width: 2.4rem;
  transition: transform 0.3s ease;
}
.collapse-header .chevron.open {
  transform: rotate(180deg);
}
.collapse-content {
  background-color: #f6f6f6;
  color: #000;
  font-size: 1.4rem;
  padding: 2rem;
  display: none;
}
.collapse.open .collapse-content {
  display: block;
}