.collapse {
    width: 100%;
    max-width: 144rem;
    margin: 0 auto;
    border-radius: 0.625rem;
    background-color: rgba(255, 96, 96, 1);
    overflow: hidden;

    // ‚úÖ Par d√©faut, hauteur fixe
    height: 5.4rem; // 54px
    transition: height 0.3s ease;

    &.open {
        height: auto; // üîÅ Quand on ouvre, on laisse la hauteur s‚Äôadapter au contenu
    }

    &-header {
        height: 5.4rem; // 54px
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 2.4rem;
        font-size: 1.6rem;
        color: white;
        cursor: pointer;

        .chevron {
            width: 2.4rem;
            transition: transform 0.3s ease;

            &.open {
                transform: rotate(180deg);
            }
        }
    }

    &-content {
        background-color: #f6f6f6;
        color: #000;
        font-size: 1.4rem;
        padding: 2rem;
        display: none; // ‚úÖ Cach√© tant que non ouvert
    }

    &.open .collapse-content {
        display: block; // ‚úÖ S'affiche uniquement quand .open est activ√©e
    }
}